import React, { useState } from "react"
import { Link } from "gatsby"

const Nav = ({ classNames }) => {
  const [expanded, toggle] = useState(false)
  const classes = ["nav"]

  if (classNames) {
    classes.push(...classNames)
  }

  return (
    <div className={classes.join(" ")}>
      <ul className={expanded ? "expanded" : ""}>
        <li className={expanded ? "icon expanded" : "icon"}>
          <button onClick={() => toggle(!expanded)}>
            <div className="bar1"></div>
            <div className="bar2"></div>
            <div className="bar3"></div>
          </button>
        </li>
        <li className={expanded ? "expanded" : ""}>
          <Link to="/" itemProp="url">
            <span>Home</span>
          </Link>
        </li>
        <li className={expanded ? "expanded" : ""}>
          <Link to="/wiki" itemProp="url">
            <span>Wiki</span>
          </Link>
        </li>
        <li className={expanded ? "expanded" : ""}>
          <Link to="/" itemProp="url">
            <span>About</span>
          </Link>
        </li>
      </ul>
    </div>
  )
}

export default Nav
